<template>
  <div class="customer-base">
    <div class="mainViewCell">
      <div class="mainViewCell-leftBox">
        <!-- ------------------------------- -->
        <div :class="isCollapse?'MenuBtn transform-arrow':'MenuBtn '" @click="MenuBtnClick"></div>
        <el-menu
          :default-active="firstPage"
          class="el-menu-vertical-demo"
          @select="selectFun"
          :collapse="isCollapse"
          style="background-color:RGB(24,79,168)"
        >
          <template v-for="(item,itemIndex) in MenuArr">
            <router-link :to="item.to" :key="itemIndex">
              <el-menu-item :index="`${itemIndex+1}`" style="text-align: left;">
                <i :class="item.iName" style="color:white"></i>
                <span slot="title">{{item.name}}</span>
              </el-menu-item>
            </router-link>
          </template>
        </el-menu>
        <!-- ---------------------------------->
      </div>
      <!-- =========================第二视口 -->
      <div class="mainViewCell-rightBox">
        <router-view name="CustomerView"></router-view>
      </div>
      <!-- ========================= 第二视口-->
    </div>
  </div>
</template>
<style scoped>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;

  background-color: RGB(28, 99, 200);
}
.el-menu {
  border-right: none;
}
.el-menu-item,
.el-submenu {
  background-color: RGB(28, 99, 200);
}
.el-menu-item,
.el-submenu__title {
  color: RGB(124, 176, 253);
}

.el-menu-item.is-active {
  color: RGB(234, 251, 6);
}
/* ========== */
.el-menu-item:hover,
.el-menu-item:focus {
  background-color: #1c63c8 !important;
}
/* =========== */
</style>

<script>
export default {
  created() {

  },
  data() {
    return {
      firstPage: this.$store.state.clientViewIndex,
      isCollapse: false,
      MenuArr: [
        {
          to: "/baseInfo",
          iName: "el-icon-location",
          name: "基本信息"
        },
        {
          to: "/cashFlow",
          iName: "el-icon-location",
          name: "资金流水"
        },
        {
          to: "/commissioning",
          iName: "el-icon-location",
          name: "委托流水"
        },
        {
          to: "/exchangeWater",
          iName: "el-icon-location",
          name: "成交流水"
        },
      ]
    };
  },
  methods: {
    selectFun(a) {
      this.storage.set("clientViewIndex", a);
      this.$store.commit("clientViewIndex", {
        clientViewIndex: a
      });
    },
    MenuBtnClick() {
      this.isCollapse = !this.isCollapse;
    }
  }
};
</script>
<style lang="scss" scoped>
.customer-base{
  .mainViewCell-leftBox{
    height:100vh;
  }
  .mainViewCell-rightBox{
    height: 100vh;
  }
}
</style>


